import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';

import '../css/App.css'

//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"

class ErrorComponent extends React.Component {
  constructor(props){
    super(props); 
  }

  render(){  

    var custom_error = ''

    if(this.props.error.toString() === 'TypeError: Failed to fetch') {
      custom_error = this.props.error.toString() + " " + "ERR_CONNECTION_REFUSED.Server is down"
    } else {
      custom_error = this.props.error.toString()
    }

    return (
        <div>
          <table className = "table table-striped">
              <thead class="thead-dark">
                 <tr>
                   <th>Error</th>
                 </tr>
              </thead> 
              <tbody>
                  <tr>
                      <td class="text-danger">{custom_error}</td>
                  </tr>   
              </tbody>  
          </table>
        </div>
    );
  }
}
export default ErrorComponent;
