import React from 'react';
//Bootstrap and jQuery libraries
import Routes from './components/Routes';
import LoginComponent from './components/Login';


class App extends React.Component {

  constructor(props){
    super(props);

    this.state = {
      token: '',
    }

  //  this.GRAPHQL_URL = 'https://devetlflow.startv.com/api/etlflow'
  //  this.GRAPHQL_LOGIN_URL = 'https://devetlflow.startv.com/api/login'

   this.GRAPHQL_URL = '/api/etlflow'
   this.GRAPHQL_LOGIN_URL = 'api/login'
   
   this.doLogin = this.doLogin.bind(this)
   this.doLogout = this.doLogout.bind(this)

  }

  doLogin(args_token,args_user) {
    this.setState({
      token: args_token
    })
  }


  doLogout(args_token) {
    this.setState({
      token: ''
    })
  }


  render(){
    if(localStorage.getItem("Authorization") === null){
      return (  
          <div>
            <LoginComponent  GRAPHQL_LOGIN_URL = {this.GRAPHQL_LOGIN_URL} doLogin = {this.doLogin}/>  
          </div>
      );
    } else {
      return (  
        <div>
          <Routes  GRAPHQL_URL = {this.GRAPHQL_URL} doLogout = {this.doLogin}/>  
        </div>
     );
    }
  }
}

export default App;
